.tu-switch {
  display: inline-flex;
  align-items: center;
  position: relative;
  font-size: 12px;
  line-height: 22px;
  vertical-align: middle; }
  .tu-switch.is-disabled .tu-switch__core {
    cursor: not-allowed; }
    .tu-switch.is-disabled .tu-switch__core::before {
      background: #a79efd; }
    .tu-switch.is-disabled .tu-switch__core::after {
      background-color: #bec8e4; }
  .tu-switch__input {
    display: inline-flex;
    align-items: center; }
    .tu-switch__core {
      display: inline-flex;
      align-items: center;
      width: 44px;
      height: 22px;
      border-radius: 40px;
      position: relative;
      cursor: pointer;
      box-shadow: 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff; }
      .tu-switch__core::before {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        border-radius: 20px;
        background: linear-gradient(330deg, #510ccf 0%, #6d5dfc 50%, #8abdff 100%);
        transition: all .4s ease;
        opacity: 0; }
      .tu-switch__core::after {
        content: "";
        position: absolute;
        top: 3px;
        left: 3px;
        border-radius: 50%;
        transition: all .4s ease;
        width: 16px;
        height: 16px;
        background-color: #9baacf; }
    .tu-switch__label {
      position: absolute;
      font-size: 12px;
      cursor: pointer;
      background: transparent;
      width: 20px;
      text-align: center;
      transition: all .4s; }
      .tu-switch__label--active {
        left: 5px;
        color: #ffffff; }
      .tu-switch__label--inactive {
        right: 5px;
        color: #9baacf; }
    .tu-switch__original {
      position: absolute;
      width: 0;
      height: 0;
      opacity: 0;
      margin: 0; }
  .tu-switch.is-checked .tu-switch__core::before {
    opacity: 1; }
  .tu-switch.is-checked .tu-switch__core::after {
    left: 100%;
    margin-left: -19px;
    background: #E4EBF5; }
  .tu-switch--small .tu-switch__input .tu-switch__core {
    width: 36px;
    height: 18px; }
    .tu-switch--small .tu-switch__input .tu-switch__core::after {
      top: 2px;
      left: 2px;
      width: 14px;
      height: 14px; }
  .tu-switch--small .tu-switch__input .tu-switch__label {
    font-size: 10px; }
    .tu-switch--small .tu-switch__input .tu-switch__label--active {
      left: 3px; }
    .tu-switch--small .tu-switch__input .tu-switch__label--inactive {
      right: 3px; }
  .tu-switch--small.is-checked .tu-switch__core::after {
    left: 100%;
    margin-left: -16px; }
  .tu-switch--large .tu-switch__input .tu-switch__core {
    width: 60px;
    height: 28px; }
    .tu-switch--large .tu-switch__input .tu-switch__core::after {
      top: 4px;
      left: 4px;
      width: 20px;
      height: 20px; }
  .tu-switch--large .tu-switch__input .tu-switch__label {
    font-size: 14px; }
    .tu-switch--large .tu-switch__input .tu-switch__label--active {
      left: 11px; }
    .tu-switch--large .tu-switch__input .tu-switch__label--inactive {
      right: 11px; }
  .tu-switch--large.is-checked .tu-switch__core::after {
    left: 100%;
    margin-left: -24px; }
